import React, { useState, useEffect, useRef } from "react";

import {
  Row,
  Col,
  Image,
  Card,
  Typography,
  Carousel,
  Tag,
  Button,
  Spin,
  Table,
  Pagination,
} from "antd";

import { useParams } from "react-router-dom";
import { connect } from "react-redux";
import moment from "moment";
import noImage from "../../../../assets/images/noimg.png";
import PageTitle from "../../../../layout/components/content/page-title";
import {
  getMyStockById,
  getVehicleAttendances,
  setVehicleAttendanceParams,
  getQRcodeactivity,
  setQRCodeactivityParams,
  qrCodeGenerate,
} from "../../../../store/sideMenu/mystock/mystockSlice";
import brokenImg from "../../../../assets/images/broken_img.png";
import { LeftOutlined, RightOutlined } from "@ant-design/icons";
// import { useRef } from "react";

function MyStockView(props) {
  let { id } = useParams();
  const ref = useRef();

  const { Text, Link, Title } = Typography;

  useEffect(() => {
    props.getMyStockById(id);
  }, []);
  const tabList = [
    {
      key: "1",
      tab: "Vehicle  Attendance",
    },

    {
      key: "2",
      tab: "QR Code Activity",
    },
  ];

  const imgStyle = {
    width: "100%",
    height: "340px",
    object_fit: "contain",
  };
  const columnClass =
    "hp-p1-body hp-text-color-black-100 hp-text-color-dark-0 hp-font-weight-600 ";
  const columns = [
    {
      title: "S.No",
      dataIndex: "index",
      width: 10,
    },

    {
      title: "Counter Name",
      render: (row) => <span className={columnClass}>{row.counter?.name}</span>,
      width: 120,
    },
    {
      title: "Transition Type",
      width: 120,
      render: (row) => (
        <span className={columnClass}>{row?.transitiontype_name}</span>
      ),
    },
    {
      title: "Created By",
      width: 120,
      render: (row) => (
        <span className={columnClass}>{row?.createdby?.fullname}</span>
      ),
    },
    {
      title: "Attendance Type",
      width: 120,
      render: (row) => (
        <span className={columnClass}>{row?.attendancetype_name}</span>
      ),
    },
    {
      title: "Date",
      width: 120,
      render: (row) => (
        <span className={columnClass}>
          {moment(row?.datetime).format("DD-MM-YYYY hh:mm a")}
        </span>
      ),
    },
  ];
  const data = props.vehicleAttendancesList.map((row, key) => ({
    ...row,
    // index: key + 1,
    index:
      (props.vehicleAttendanceParams.page - 1) *
        props.vehicleAttendanceParams.page_size +
      (key + 1),
  }));

  const qrCodeColumns = [
    {
      title: "S.No",
      dataIndex: "index",
      width: 10,
    },

    {
      title: "User Name",

      render: (row) => (
        <span className={columnClass}>{row.user?.fullname}</span>
      ),
      width: 120,
    },
    {
      title: "QR Activity Type",
      width: 120,
      render: (row) => (
        <span className={columnClass}>{row?.qractivitytype_name}</span>
      ),
    },
    {
      title: "Created On",
      width: 120,
      render: (row) => (
        <span className={columnClass}>
          {moment(row?.createdon).format("DD-MM-YYYY hh:mm a")}
        </span>
      ),
    },
  ];
  const qrCodeData = props.qrCodeactivityList.map((row, key) => ({
    ...row,
    // index: key + 1,
    index:
      (props.qrCodeactivityParams.page - 1) *
        props.qrCodeactivityParams.page_size +
      (key + 1),
  }));

  const [vehicleActivityTab, setVehicleActivityTab] = useState("1");
  const onTabChange = (key) => {
    setVehicleActivityTab(key);
    if (key == "1") {
      props.setVehicleAttendanceParams({
        ...props.vehicleAttendanceParams,
        page: 1,
        purchase: props.myStockData.id,
      });
      props.getVehicleAttendances({
        ...props.vehicleAttendanceParams,
        page: 1,
        purchase: props.myStockData.id,
      });
    } else if (key == "2") {
      props.setQRCodeactivityParams({
        ...props.qrCodeactivityParams,
        page: 1,
        purchase: props.myStockData.id,
      });
      props.getQRcodeactivity({
        ...props.qrCodeactivityParams,
        page: 1,
        purchase: props.myStockData.id,
      });
    }
  };
  function onPageChange(page, pageSize) {
    if (vehicleActivityTab == "1") {
      props.setVehicleAttendanceParams({
        ...props.vehicleAttendanceParams,
        page_size: pageSize,
        page: page == 0 ? 1 : page,
        purchase: props.myStockData.id,
      });
      props.getVehicleAttendances({
        ...props.vehicleAttendanceParams,
        page_size: pageSize,
        page: page == 0 ? 1 : page,
        purchase: props.myStockData.id,
      });
    } else if (vehicleActivityTab == "2") {
      props.setQRCodeactivityParams({
        ...props.qrCodeactivityParams,
        page_size: pageSize,
        page: page == 0 ? 1 : page,
      });
      props.getQRcodeactivity({
        ...props.qrCodeactivityParams,
        page_size: pageSize,
        page: page == 0 ? 1 : page,
      });
    }

    // props.clearStockList();
    // props.setMyStockParams({
    //   ...props.myStockParams,
    // page_size: pageSize,
    // page: page == 0 ? 1 : page,
    // });
    // props.getMyStockList({
    //   ...props.myStockParams,
    // page_size: pageSize,
    // page: page == 0 ? 1 : page,
    // });
  }

  function showTotal(total) {
    return "Total " + total + " items";
  }

  return (
    <div>
      <PageTitle pageTitle="My Stock View" goBack={true} />

      <Row className="hp-ecommerce-app-detail hp-mb-10 hp-mt-16">
        <Col span={24}>
          <Card
            className="hp-border-color-black-40"
            title={
              <>
                <Title className="hp-p-0" level={5}>
                  {props.myStockData.counter?.name}
                </Title>
                <Text type="secondary">
                  Mobile:{" "}
                  {props.myStockData.counter?.mobile != null &&
                  props.myStockData.counter?.mobile != ""
                    ? props.myStockData.counter?.mobile
                    : "None"}
                </Text>
              </>
            }
            extra={
              <>
                {props.userAccessList?.indexOf("Attendance.add_qrcode") !==
                  -1 && (
                  <Button
                    disabled={props.myStockData.availability ? false : true} //
                    type="primary"
                    size="large"
                    onClick={() => {
                      props.qrCodeGenerate({
                        purchase_id: props.myStockData.id,
                        qractivitytype: !props.QRCodeStatus ? 1 : 2,
                      });
                    }}
                  >
                    {!props.QRCodeStatus ? "Generate" : "Re Generate"} QR Code
                  </Button>
                )}
              </>
            }
            actions={[
              <>
                <p className="hp-mb-0 hp-caption hp-font-weight-400 hp-text-color-black-80 hp-text-color-dark-30">
                  Purchase Rate
                </p>
                <Title className="hp-p-0" level={4}>
                  {props.myStockData.purchaserate}{" "}
                </Title>
              </>,
              <>
                <p className="hp-mb-0 hp-caption hp-font-weight-400 hp-text-color-black-80 hp-text-color-dark-30">
                  Repair Amount
                </p>
                <Title className="hp-p-0" level={4}>
                  {props.myStockData.repair_amount}{" "}
                </Title>
              </>,
              <>
                <p className="hp-mb-0 hp-caption hp-font-weight-400 hp-text-color-black-80 hp-text-color-dark-30">
                  Re-Estimation Amount
                </p>
                <Title className="hp-p-0" level={4}>
                  {props.myStockData.re_estimation_amount}{" "}
                </Title>
              </>,
            ]}
          >
            <Row gutter={[24, 24]}>
              <Col
                lg={14}
                span={24}
                className="hp-border-1 hp-border-color-dark-80 hp-border-radius"
              >
                <Image.PreviewGroup>
                  <Carousel autoplay arrows effect="fade" ref={ref} draggble>
                    {props.stockImages.map((item, index) => (
                      <>
                        <div className="hp-text-center hp-m-8 ">
                          <Title className="hp-p-0" level={4}>
                            {item.imageType}
                          </Title>
                        </div>
                        <div
                          key={index}
                          className="hp-slick-slide-item hp-mx-4 hp-d-flex-full-center"
                        >
                          {item.file != null ? (
                            <Image
                              src={item.file}
                              style={imgStyle}
                              preview={true}
                              onError={({ currentTarget }) => {
                                currentTarget.onerror = null; // prevents looping
                                currentTarget.src = brokenImg;
                              }}
                            />
                          ) : (
                            <Image
                              src={noImage}
                              preview={false}
                              style={imgStyle}
                            />
                          )}
                        </div>
                      </>
                    ))}
                  </Carousel>
                </Image.PreviewGroup>

                <div className="hp-m-10 hp-text-center">
                  <Button
                    onClick={() => {
                      ref.current.prev();
                    }}
                    className="hp-mr-10"
                  >
                    <LeftOutlined size={24} />
                  </Button>
                  {/* <Button
                    onClick={() => {
                      ref.current.goTo(0);
                    }}
                  >
                    Reset
                  </Button> */}
                  <Button
                    onClick={() => {
                      ref.current.next();
                    }}
                  >
                    <RightOutlined size={24} />
                  </Button>
                </div>
              </Col>

              <Col lg={10} span={24}>
                <Card
                  title={
                    <>
                      <Title className="hp-p-0" level={5}>
                        {props.myStockData.vehicle?.vehicleno}{" "}
                      </Title>
                      <Text type="secondary">
                        {props.myStockData.vehicle?.version?.name}
                      </Text>
                    </>
                  }
                  extra={
                    <Tag
                      color={props.myStockData.availability ? "green" : "red"}
                    >
                      {props.myStockData.availability
                        ? "Available"
                        : "Not Available"}
                    </Tag>
                  }
                >
                  <Row gutter={[24, 16]}>
                    <Col md={12} span={24}>
                      <p className="hp-mb-0 hp-caption hp-font-weight-400 hp-text-color-black-80 hp-text-color-dark-30">
                        Document Number :
                      </p>
                      <span className="hp-d-block hp-p1-body hp-font-weight-600 hp-text-color-black-100 hp-text-color-dark-0">
                        {props.myStockData.code}
                      </span>
                    </Col>
                    <Col md={12} span={24}>
                      <p className="hp-mb-0 hp-caption hp-font-weight-400 hp-text-color-black-80 hp-text-color-dark-30">
                        Brand :
                      </p>
                      <span className="hp-d-block hp-p1-body hp-font-weight-600 hp-text-color-black-100 hp-text-color-dark-0">
                        {props.myStockData.vehicle?.version?.brand?.name}
                      </span>
                    </Col>

                    <Col md={12} span={24}>
                      <p className="hp-mb-0 hp-caption hp-font-weight-400 hp-text-color-black-80 hp-text-color-dark-30">
                        Brand Family:
                      </p>
                      <span className="hp-d-block hp-p1-body hp-font-weight-600 hp-text-color-black-100 hp-text-color-dark-0">
                        {props.myStockData.vehicle?.version?.brandfamily?.name}
                      </span>
                    </Col>

                    <Col md={12} span={24}>
                      <p className="hp-mb-0 hp-caption hp-font-weight-400 hp-text-color-black-80 hp-text-color-dark-30">
                        Vehicle Model:
                      </p>
                      <span className="hp-d-block hp-p1-body hp-font-weight-600 hp-text-color-black-100 hp-text-color-dark-0">
                        {props.myStockData.vehicle?.version?.model?.name}
                      </span>
                    </Col>

                    <Col md={12} span={24}>
                      <p className="hp-mb-0 hp-caption hp-font-weight-400 hp-text-color-black-80 hp-text-color-dark-30">
                        Vehicle Year:
                      </p>
                      <span className="hp-d-block hp-p1-body hp-font-weight-600 hp-text-color-black-100 hp-text-color-dark-0">
                        {props.myStockData.vehicle?.year}
                      </span>
                    </Col>

                    <Col md={12} span={24}>
                      <p className="hp-mb-0 hp-caption hp-font-weight-400 hp-text-color-black-80 hp-text-color-dark-30">
                        Vehicle Type:
                      </p>
                      <span className="hp-d-block hp-p1-body hp-font-weight-600 hp-text-color-black-100 hp-text-color-dark-0">
                        {props.myStockData.vehicle?.version?.vehicletype?.name}
                      </span>
                    </Col>

                    <Col md={12} span={24}>
                      <p className="hp-mb-0 hp-caption hp-font-weight-400 hp-text-color-black-80 hp-text-color-dark-30">
                        Emission Name:
                      </p>
                      <span className="hp-d-block hp-p1-body hp-font-weight-600 hp-text-color-black-100 hp-text-color-dark-0">
                        {props.myStockData.vehicle?.emission?.name}
                      </span>
                    </Col>

                    <Col md={12} span={24}>
                      <p className="hp-mb-0 hp-caption hp-font-weight-400 hp-text-color-black-80 hp-text-color-dark-30">
                        Vehicle Priority:
                      </p>
                      <span className="hp-d-block hp-p1-body hp-font-weight-600 hp-text-color-black-100 hp-text-color-dark-0">
                        {props.myStockData.vehicle?.version?.priority?.name}
                      </span>
                    </Col>

                    <Col md={12} span={24}>
                      <p className="hp-mb-0 hp-caption hp-font-weight-400 hp-text-color-black-80 hp-text-color-dark-30">
                        Engine Number:
                      </p>
                      <span className="hp-d-block hp-p1-body hp-font-weight-600 hp-text-color-black-100 hp-text-color-dark-0">
                        {props.myStockData.vehicle?.engineno}
                      </span>
                    </Col>

                    <Col md={12} span={24}>
                      <p className="hp-mb-0 hp-caption hp-font-weight-400 hp-text-color-black-80 hp-text-color-dark-30">
                        Chassis Number:
                      </p>
                      <span className="hp-d-block hp-p1-body hp-font-weight-600 hp-text-color-black-100 hp-text-color-dark-0">
                        {props.myStockData.vehicle?.chassisno}
                      </span>
                    </Col>

                    <Col md={12} span={24}>
                      <p className="hp-mb-0 hp-caption hp-font-weight-400 hp-text-color-black-80 hp-text-color-dark-30">
                        Vehicle Reading:
                      </p>
                      <span className="hp-d-block hp-p1-body hp-font-weight-600 hp-text-color-black-100 hp-text-color-dark-0">
                        {props.myStockData.vehiclereading}
                      </span>
                    </Col>

                    <Col md={12} span={24}>
                      <p className="hp-mb-0 hp-caption hp-font-weight-400 hp-text-color-black-80 hp-text-color-dark-30">
                        Fuel Type:
                      </p>
                      <span className="hp-d-block hp-p1-body hp-font-weight-600 hp-text-color-black-100 hp-text-color-dark-0">
                        {props.myStockData.vehicle?.version?.fueltype_name}
                      </span>
                    </Col>
                  </Row>
                </Card>
              </Col>
            </Row>
          </Card>
          <Card
            className="hp-mt-10"
            // title="Default size card"
            // tabBarExtraContent={<a href="#">More</a>}
            tabList={tabList}
            activeTabKey={vehicleActivityTab}
            onTabChange={(key) => {
              onTabChange(key);
            }}
          >
            {vehicleActivityTab == "1" && (
              <Spin tip="Loading..." spinning={props.loading} delay={500}>
                <Table
                  columns={columns}
                  rowClassName="hp-btn-bg-color-none"
                  dataSource={data}
                  // scroll={{ x: 1300 }}
                  pagination={false}
                  size="middle"
                />
                <Pagination
                  size="small"
                  current={props.vehicleAttendanceParams?.page}
                  pageSize={props.vehicleAttendanceParams?.page_size}
                  total={props.vehicleAttendanceslistCount}
                  showTotal={showTotal}
                  showSizeChanger
                  onChange={onPageChange}
                  className="hp-mb-12 hp-mt-24 hp-float-right"
                />
              </Spin>
            )}
            {vehicleActivityTab == "2" && (
              <Spin tip="Loading..." spinning={props.loading} delay={500}>
                <Table
                  columns={qrCodeColumns}
                  rowClassName="hp-btn-bg-color-none"
                  dataSource={qrCodeData}
                  // scroll={{ x: 1300 }}
                  pagination={false}
                  size="middle"
                />
                <Pagination
                  size="small"
                  current={props.qrCodeactivityParams?.page}
                  pageSize={props.qrCodeactivityParams?.page_size}
                  total={props.qrCodeactivitylistCount}
                  showTotal={showTotal}
                  showSizeChanger
                  onChange={onPageChange}
                  className="hp-mb-12 hp-mt-24 hp-float-right"
                />
              </Spin>
            )}
          </Card>
        </Col>
      </Row>
      {/* {props.QRCodeModel && <QRCodeModelView />} */}
    </div>
  );
}

function mapStateToProps({ mystock, system }) {
  return {
    myStockData: mystock.myStockData,
    stockImages: mystock.stockImages,
    loading: mystock.loading,
    vehicleAttendancesList: mystock.vehicleAttendancesList,
    vehicleAttendanceParams: mystock.vehicleAttendanceParams,
    vehicleAttendanceslistCount: mystock.vehicleAttendanceslistCount,
    qrCodeactivityList: mystock.qrCodeactivityList,
    qrCodeactivityParams: mystock.qrCodeactivityParams,
    qrCodeactivitylistCount: mystock.qrCodeactivitylistCount,
    QRCodeStatus: mystock.QRCodeStatus,
    userAccessList: system.userAccessList,
  };
}

export default connect(mapStateToProps, {
  getMyStockById,
  getVehicleAttendances,
  setVehicleAttendanceParams,
  getQRcodeactivity,
  setQRCodeactivityParams,
  qrCodeGenerate,
})(MyStockView);
